@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&display=swap);
body {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: "Raleway", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.profile-picture-container {
  text-align: center;
  padding-top: 15px;
}

#dogDad {
  height: 200px;
  width: 200px;
  border-radius: 50%;
  animation: fadeIn ease 10s;
  -webkit-animation: fadeIn ease 10s;
  -moz-animation: fadeIn ease 10s;
  -o-animation: fadeIn ease 10s;
  -ms-animation: fadeIn ease 10s;
}

.row {
  box-shadow: 2px 2px 1px 1px #888888;
  border: 1px solid #504f4f;
  border-radius: 5px;
  padding: 5px;
}

.navbar-container {
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-right: 1px solid #c9c9c9;
  padding-right: 70px;
  margin-top: 40px;
}

.link {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.671);
}

.information-component {
  height: 50%;
}

.information-container {
  text-align: center;
}

.aboutme-content-row {
  height: 50vh;
  overflow-y: auto;
}
.aboutme-content-row-mobile {
  padding-left: 10px;
  padding-right: 10px;
}

.projects-title-row {
  margin-bottom: 20px;
}

img {
  width: 90%;
  height: 50%;
}

h5 {
  padding-left: 30%;
  padding-right: 30%;
  padding-bottom: 10px;
  padding-top: 10px;
  border-bottom: 1px solid #c9c9c9;
  border-top: 1px solid #c9c9c9;
}

.projects-info-row {
  height: 44vh;
  overflow-y: auto;
}

.projects-info-row-mobile p {
  text-align: left;
  padding-left: 15px;
  padding-right: 15px;
}

.projects-nl-container,
.projects-tq-container {
  margin-top: 100px;
}

.projects-evc-links,
.projects-nl-links {
  text-align: left;
  display: flex;
  justify-content: space-between;
}

.contact-email-row,
.contact-github-row,
.contact-linkedin-row {
  margin-top: 40px;
}

.contact-component-mobile {
  padding-left: 15px;
  padding-right: 15px;
}

.mnavbar-about-row {
  padding-bottom: 8px;
  margin-bottom: 7px;
}

.mnavbar-projects-row {
  margin-bottom: 7px;
}

.mnavbar-contact-row {
  margin-top: 15px;
}

.link {
  padding-top: 8px;
  padding-bottom: 8px;
  border-bottom: 1px solid #c9c9c9;
}

.maincontent-desktop {
  background-color: white;
  margin-left: 10%;
  margin-right: 10%;
  padding-top: 5px;
  padding-bottom: 3%;
  margin-top: 10px;
  border-radius: 10px;
  height: 70%;
  color: rgba(0, 0, 0, 0.671);
  display: none;
}

.maincontent-mobile {
  background-color: white;
  margin-left: 10%;
  margin-right: 10%;
  padding-top: 5px;
  padding-bottom: 3%;
  margin-top: 10px;
  border-radius: 10px;
  height: 70vh;
  overflow-y: hidden;
  overflow-x: hidden;
  display: block;
  color: rgba(0, 0, 0, 0.671);
}

.maincontent-container {
  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.maincontent-title-row {
  border-bottom: 1px solid gray;
  align-self: center;
  width: 90%;
  display: block;
}

.maincontent-info-row {
  display: flex;
  justify-content: space-evenly;
}

.maincontent-info-col {
  width: 70%;
  overflow-y: auto;
}

.maincontent-navbar-row-mobile {
  margin-top: 20px;
  margin-bottom: 30px;
}

.maincontent-navbar-scroll-mobile {
  height: 60vh;
  overflow-y: scroll;
}

@media only screen and (max-height: 575px) {
  .maincontent-desktop {
    display: none;
  }
  .maincontent-mobile {
    display: block;
  }
}

@media only screen and (min-width: 1025px) {
  .maincontent-desktop {
    display: block;
  }
  .maincontent-mobile {
    display: none;
  }
}

.app-container {
  background: url(/Portfolio/static/media/clouds.0029ddd1.jpg) center center fixed;
  background-repeat: repeat-x;
  background-position-y: center;
  height: 120vh;
  background-size: cover;
}

.app-content {
  width: 100%;
  height: 100vh;
  text-align: center;
}

